import axios from 'axios'
import Cookie from 'js-cookie'

export default {
  getWarnDetail (params = {}, url = '/warn/getWarnConfigs') {
    axios.defaults.headers.common['project'] = window.localStorage.getItem('pjValue')
    axios.defaults.headers.common['profile'] = window.localStorage.getItem('prValue')
    params.token = Cookie.get('token')
    return axios.get(url, {params})
    // return Promise.resolve({
    //   data: {
    //     interval: '100',
    //     path: 'www.baidu.com,www.google.com'
    //   }
    // })
  },
  getWarnList (params = {}, url = '/warn/getWarnServices') {
    axios.defaults.headers.common['project'] = window.localStorage.getItem('pjValue')
    axios.defaults.headers.common['profile'] = window.localStorage.getItem('prValue')
    params.token = Cookie.get('token')
    return axios.get(url, {params})
    // return Promise.resolve({
    //   data: [
    //     'redefine-ultron',
    //     'redefine-crawler',
    //     'redefine-ertify',
    //     'redefine-fileservice',
    //     'redefine-auth',
    //     'redefine-im-router',
    //     'redefine-shortLink',
    //     'redefine-report',
    //     'redefine-search',
    //     'redefine-userservice',
    //     'redefine-download',
    //     'redefine-lbs',
    //     'redefine-erueka',
    //     'redefine-discovery',
    //     'redefine-im-connector',
    //     'redefine-gateway',
    //     'redefine-leaderboard',
    //     'redefine-config',
    //     'redefine-presentation',
    //     'redefine-push',
    //     'redefine-relationship',
    //     'redefine-statistic',
    //     'redefine-tagservice'
    //   ]
    // })
  },
  editWarn (params = {}, url = '/warn/updateWarnConfig') {
    axios.defaults.headers.common['project'] = window.localStorage.getItem('pjValue')
    axios.defaults.headers.common['profile'] = window.localStorage.getItem('prValue')
    params.token = Cookie.get('token')
    return axios.get(url, {params})
  }
}



