<i18n>
    {
    "zh": {
    "warn": {
    "operation": "操作",
    "filterMsg": "请输入过滤条件",
    "error": "操作失败！",
    "success": "操作成功！",
    "name": "名称",
    "config": "配置"
    }
    },
    "en": {
    "warn": {
    "operation": "Operation",
    "filterMsg": "Input filter key",
    "error": "Error",
    "success": "Success",
    "name": "Name",
    "config": "Config"
    }
    }
    }
</i18n>
<template>
    <div class="warn">
        <div class="search-form">
            <el-row :gutter="10">
                <el-col :span="6">
                    <el-input :placeholder="$t('warn.filterMsg')" v-model="searchKey"></el-input>
                </el-col>
                <!--<el-col :span="6" :offset="12">-->
                    <!--<el-button type="primary" @click="addService">{{$t('warn.addService')}}</el-button>-->
                <!--</el-col>-->
            </el-row>
        </div>
        <el-table  id="gather-data-form" :data="filteredList" @row-click="getWarnDetail" width="100%" align="right" :stripe="true" header-align="right">
            <el-table-column prop="name" :label="$t('warn.name')"></el-table-column>
            <el-table-column :label="$t('warn.operation')" width="200px">
                <template slot-scope="scope">
                    <el-button type="primary" size="small" @click="showDetail">{{$t('warn.config')}}</el-button>
                </template>
            </el-table-column>
        </el-table>
        <component :is="visible.dialog" :currRow="currRow" :visible="visible" @refresh="getWarnList"></component>
    </div>
</template>

<script>
  import service from './service'
  import editModal from './editModal'

  export default {
    name: 'warn',
    data () {
      return {
        searchKey: '',
        visible: {
          dialog: null
        },
        currRow: {},
        warnList: []
      }
    },
    methods: {
      showDetail () {
        console.log('showDetail', this.visible, this)
        this.visible.dialog = 'detail'
      },
      getWarnList () {
        return service.getWarnList().then(({data = []}) => {
          this.warnList = data.map(name => ({name}))
        })
      },
      getWarnDetail (row) {
        this.currRow = row
      }
    },
    computed: {
      'filteredList' () {
        return this.warnList.filter(item => item.name.includes(this.searchKey)) || []
      }
    },
    mounted () {
      console.log('mounted', this.$t('warn'))
      this.getWarnList()
    },
    components: {
      'detail': editModal
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
</style>



