<i18n>
    {
    "zh": {
    "warn": {
    "interval": "相同异常重复发送时间间隔(s)",
    "filteredPath": "过滤Path",
    "submit": "提交",
    "cancel": "取消",
    "edit": "编辑",
    "error": "操作失败！",
    "success": "操作成功！",
    "notEmpty": "不能为空"
    }
    },
    "en": {
    "warn": {
    "interval": "Interval",
    "filteredPath": "Filtered path",
    "submit": "Submit",
    "cancel": "Cancel",
    "edit": "Edit",
    "error": "Error",
    "success": "Success",
    "notEmpty": "Cannot be empty"
    }
    }
    }
</i18n>

<template>
    <el-dialog :title="$t('warn.edit')" :visible='true' class="warn-edit-modal" @close="close" :modal="false">
        <el-form :model="detail" label-position="left" ref="form" :rules="rules">
            <el-form-item prop="interval" :label="$t('warn.interval')">
                <el-input v-model="detail.interval"></el-input>
            </el-form-item>
            <el-form-item :label="$t('warn.filteredPath')">
                <el-input v-model="newPath">
                    <template slot="append">
                        <i class="el-icon-plus pointer" @click="addPath"></i>
                    </template>
                </el-input>
            </el-form-item>
            <el-form-item label="" v-for="(key, idx) in detail.path" :key="idx">
                <el-input v-model="detail.path[idx]">
                    <template slot="append">
                        <i class="el-icon-delete pointer" @click="deletePath(idx)"></i>
                    </template>
                </el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="submit" type="primary">{{$t('warn.submit')}}</el-button>
            <el-button @click="close">{{$t('warn.cancel')}}</el-button>
        </div>
    </el-dialog>
</template>

<script>
  import service from './service'

  export default {
    name: 'edit',
    data () {
      return {
        newPath: '',
        rules: {
          // interval: [
          //   {
          //     required: true,
          //     trigger: 'blur',
          //     message: this.$t('warn.notEmpty')
          //   }
          // ]
        },
        detail: {
          interval: '',
          path: []
        }
      }
    },
    props: {
      currRow: {
        updateType: Object,
        required: true
      },
      visible: {
        updateType: Object,
        required: true
      }
    },
    watch: {},
    methods: {
      deletePath (idx) {
        this.detail.path.splice(idx, 1)
      },
      addPath () {
        this.detail.path.push(this.newPath)
        this.newPath = ''
      },
      submit () {
        this.$refs['form'].validate(valid => {
          if (valid) {
            var params = {
              data: Object.assign({}, this.currRow, this.detail)
            }
            params.data.path = params.data.path.join(',')
            service.editWarn(params).then(res => {
              this.$message({
                type: res.data ? 'success' : 'error',
                message: res.data ? this.$t('warn.success') : this.$t('warn.error')
              })
              if (res) {
                this.visible.dialog = null
              }
            })
          }
        })
      },
      close () {
        this.visible.dialog = null
      },
      getDetail () {
        var params = this.currRow
        return service.getWarnDetail(params).then(({data: res}) => {
          res.path = res.path ? res.path.split(',') : []
          this.detail = res
        })
      }
    },
    mounted () {
      this.getDetail()
    }
  }
</script>

<style>
    .warn-edit-modal .el-form-item__content {
        margin-left: 210px;
    }
    label.el-form-item__label:after {
        content: " : ";
        font-weight: bold;
    }
</style>



